import React from 'react'
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import styled from 'styled-components'
import StyledMain from '../Components/UtilityComponents';

const CategoriesWrapper = styled.div `
    display: flex;
    justify-content: space-evenly;
    padding: 50px 0;
    width: 90%;
    margin: auto;
`;
const ProjectsWrapper = styled.div `
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(375px, 1fr));
    gap: 15px;
    padding-bottom: 50px;   
    width: 90%;
    margin: 0 auto;

`;

const CategoryButton = styled.button `
    height: 50px;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 2px solid #3C403D; */
    border: 0;
    cursor: pointer;
    color: #fff;
    background: unset;
    font-weight: bolder;
    margin:5px;

    &:hover {
        background: rgb(0, 0, 0, 0.25);
        color: #fff;
    }

    &:focus {
        outline: none;
    }

    &.activeCategory {
        border: 2px solid #fff;
    }
`

const CATEGORIES = ['SHOW ALL', 'GAMES', 'COMPUTER GRAPHICS', 'PROGRAMMING', 'WEBB'];

function Projects({ selectedCat, onClickFunction, projects }) {
    let match = useRouteMatch();

    function DisplayCategories() {
        return (
            <CategoriesWrapper>
                {
                    CATEGORIES.map((category, index) => (
                        <CategoryButton key={index} className={category === selectedCat ? 'activeCategory' : 'buttonAnimation'}
                            onClick={() => onClickFunction(category)}
                        >
                            {category}
                        </CategoryButton >
                    ))
                }
            </CategoriesWrapper >
        );
    }

    return (
        <StyledMain>
            <DisplayCategories />
            <ProjectsWrapper>
                {projects.map(project => (
                    //Creates a relative link to all the different projects using its ID
                    <Link className="" style={{/*padding: "5px", background: "white"*/}} key={project.id} to={`${match.url}/${project.id}`}>
                        <ProjectThumbnail title={project.title} backgroundImg={project.backgroundURL} />
                    </Link>
                ))}
            </ProjectsWrapper>
        </StyledMain>
    );
}


const ProjectContainer = styled.div `
    background: white;
    overflow: hidden;
    position: relative;


    &:before {
        content: "";
        padding: 0 0px 60% 0; /* Adjust height of the cards */ 
        display: block;
    }
`; 

const StyledImg = styled.img `
    background: white;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
`;

const StyledProjectTitle = styled.div `
    background: #07145200;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    font-size: 2.5em;
    font-weight: 600;
    transition: 0.4s ease;
    opacity: 0;

    &:hover {
        background: #0714528c;
        background: #00000061;
        opacity: 1;
    }
`;

function ProjectThumbnail({ title, backgroundImg }) {
    let style = { backgroundImage: `url(${backgroundImg})`, backgroundColor: '#ececec' }
    //<img src={require('../images/placeholder250.png')}></img>
    return (
        <ProjectContainer>
            <StyledImg src={backgroundImg}></StyledImg>
            <StyledProjectTitle><span>{title}</span></StyledProjectTitle>
        </ProjectContainer>
    );
}

export default Projects;

